/* base doc */
const baseDoc = () => ({
    _id: "",
    name: "",
    code: "",
    exchangeRateINR: 0,
    zeroDecimal: false,
    adminEnabled: false,
    clientEnabled: false,
    defaultInflationPercentage: 20,
    description: "",
    status: false
})

export default {
  props: {
    model: String,
  },
  data() {
    return {
      editMode: false,
      doc: baseDoc(),
      loading: false,
      updated: false,
    };
  },
  methods: {
    async updateDocument() {
      this.loading = true;
      const result = await this.$updateDocument(
        this.model,
        this.doc,
        this.editMode
      );
      this.loading = false;

      if (!result.updated) return;

      this.$emit("updated");
      this.populateForm(result.doc);
      this.$flash(this);
    },
    async deleteDocument() {
      this.loading = true;
      const result = await this.$deleteDocument(this.model, this.doc._id);
      this.loading = false;

      if (!result.deleted) return;

      this.$emit("updated");
      this.resetForm();
      this.$flash(this);
    },
    populateForm(details) {
      const {
        _id,
        name,
        code,
        exchangeRateINR,
        zeroDecimal,
        adminEnabled,
        clientEnabled,
        defaultInflationPercentage,
        description,
        status
      } = details;

      this.doc = {
        _id,
        name,
        code,
        exchangeRateINR,
        zeroDecimal,
        adminEnabled,
        clientEnabled,
        defaultInflationPercentage,
        description,
        status
      };

      if(typeof defaultInflationPercentage === 'String') {
        this.doc.defaultInflationPercentage = parseInt(defaultInflationPercentage)
      }

      this.editMode = true;
    },
    closeForm() {
      this.resetForm();
      this.$emit("close");
    },
    resetForm() {
      this.populateForm(baseDoc());
      this.editMode = false;
    },
  },
};
